import React, {Suspense, useEffect, useState} from "react";
import './computer.css';
import {Canvas} from "@react-three/fiber";
import {OrbitControls, Preload, useGLTF} from "@react-three/drei";
import Loader from "../../components/loader/Loader";


type ComputerProps = {
  isTablet: boolean,
  isMobile: boolean,
};

const Computer = ({ isTablet, isMobile }: ComputerProps) => {
  const computer = useGLTF("./desktop_pc/scene.gltf");

  return (
    <mesh>

      <hemisphereLight intensity={1.15} groundColor='black' />

      <spotLight
        position={[-20, 50, 10]}
        angle={0.12}
        penumbra={1}
        intensity={1}
        castShadow
        shadow-mapSize={1024}
      />
      <pointLight intensity={1} />

      <primitive
        object={computer.scene}
        scale={isMobile ? 0.3 : isTablet ? 0.4 : 0.6}
        position={isMobile ? [0, -2, -0.5] : isTablet ? [0, -3, -0.5] : [0, -3, -1.5]}
        rotation={[-0.01, -0.2, -0.1]}
        castShadow
        receiveShadow
      />

    </mesh>
  );
};


const ComputerCanvas = () => {
  const [isTablet, setIsTablet] = useState<boolean>(false);
  const [isMobile, setIsMobile] = useState<boolean>(false);

  useEffect(() => {
    const tabletMediaQuery = window.matchMedia("(max-width: 768px)");
    const mobileMediaQuery = window.matchMedia("(max-width: 576px)");

    setIsTablet(tabletMediaQuery.matches);
    setIsMobile(mobileMediaQuery.matches);

    const handleMediaQueryChange = (event: any) => {
      setIsMobile(event.matches);
    };

    tabletMediaQuery.addEventListener("change", handleMediaQueryChange);
    mobileMediaQuery.addEventListener("change", handleMediaQueryChange);
    return () => {
      tabletMediaQuery.removeEventListener("change", handleMediaQueryChange);
      mobileMediaQuery.removeEventListener("change", handleMediaQueryChange);
    };
  }, []);

  return (
    <Canvas
      frameloop='demand'
      shadows
      dpr={[1, 2]}
      camera={{ position: [20, 3, 5], fov: 25 }}
      gl={{ preserveDrawingBuffer: true }}
    >
      <Suspense fallback={<Loader />}>

        <OrbitControls
          enableZoom={false}
          maxPolarAngle={Math.PI / 2}
          minPolarAngle={Math.PI / 2}
        />

        <Computer isTablet={isTablet} isMobile={isMobile} />

      </Suspense>

      <Preload all />

    </Canvas>
  );
};

export default ComputerCanvas;
