import React from "react";
import './hero.css';
import ComputerCanvas from "src/canvas/computer/Computer";


const Hero = () => {
  
  return (
    <section id="hero" className="hero">

      <div className="hero-content">
        
        <div className="hero-marker">
          <div className="dot" />
          <div className="line" />
        </div>

        <div className="hero-text">
          <h1>
            Cześć! Nazywam się <br/><span className="highlight">Mateusz</span>
          </h1>
          <h3>Jestem frontend developerem</h3>
        </div>
      </div>

      <ComputerCanvas />

      <div className="scroll-indicator">
        <a href="#about">
          <div className="scroll-box">
            <div className="scroll-dot" />
          </div>
        </a>
      </div>

    </section>
  )
}

export default Hero;